<template>
  <v-container id="sales">
    <!-- custom component with statistic about current, previous round and cost accounting -->
    <div ref="round-data">
      <prev-cur-round-stats
        :prevAsmLine="'SmartLine'"
        :prevAsmLineCost="0.0"
        :prevNumOfAsmLines="0"
        :prevProdCosts="0.0"
        :prevProdCapac="0.0"
        :prevQuality="0.0"
        :prevWorkload="0.0"
        :prevSafety="0.0"
        :curAsmLine="'SmartLine'"
        :curAsmLineCost="0.0"
        :curNumOfAsmLines="0"
        :curProdCosts="0.0"
        :curProdCapac="0.0"
        :curQuality="0.0"
        :curWorkload="0.0"
        :curSafety="0.0"
        :budget="150000.0"
        :runningCosts="0.0"
        :avgProdCostBike="'Incomplete'"
        :estimatedQual="0.0"
        :maxProdCapac="'Incomplete'"
        :overDemand="40000.0"
        style="height: 500px;"
      />
    </div>

    <v-divider />

    <v-row v-if="this.$store.state.salesStep <= 4" class="pa-2" style="margin-top: 20px; margin-bottom: 40px;">
      <v-col align="left" cols="9">
        <div>
          <h2>{{ this.stepText }}</h2>
        </div>
      </v-col>
      <v-col align="right">
        <v-btn @click="nextSalesStep" dark rounded link :color="teamColor">
          <b>I understand</b>
        </v-btn>
      </v-col>
    </v-row>

    <!-- Managing sales process -->
    <div class="mt-6 mb-3" ref="logic" style="margin: 1px;">
    <v-tabs v-model="salesTabs" :color="teamColor" centered>
      <v-tab>
        Manage Sales Activities
      </v-tab>
      <v-tab>
        Manage Sales Prices
      </v-tab>
    </v-tabs>

    <!-- Hover Me -->
    <v-container>
      <v-col align="start" >
        <v-tooltip bottom color="black">
        <template v-slot:activator="{ on, attrs }">
        <v-btn
          :color="teamColor"
          dark
          v-bind="attrs"
          v-on="on"
          x-large
        >
        <v-icon>mdi-chat-question </v-icon>
         Hover me
        </v-btn>
        </template>
        <span>Additional sales tools that can boost the sales numbers by different coefficients. It is possible to choose more than one.</span><br>
        </v-tooltip>
      </v-col>
    </v-container>

    <!-- Manage Sales Activities -->
    <v-tabs-items v-model="salesTabs">
      <v-tab-item>
        <v-row>
          <v-col>
            <v-row>
              <v-text-field
                class="pl-2 ml-2 mt-4"
                prefix="Persons "
                type='number' 
                v-model="amount.numOfSalesPersons"
                label="Number of Sales Persons"
                :value="numOfSalesPers"
                :min="0"
                :max="1000000000"
                outlined
                hint="Only insert positiv integers here!"
              ></v-text-field>
            </v-row>
            <v-row>
              <v-text-field
                class="pl-2 ml-2"
                prefix="EUR  "
                type='number'
                :min="0"
                :max="1000000000"
                v-model="amount.pricePerBike"
                label="Price per Bike"
                outlined
                hint="Only insert a positive integers like '2500.72'!"
              ></v-text-field>
            </v-row>
          </v-col>

          <v-col>
            <v-text-field
            class="mt-1"
              label="Base Salary (EUR)"
              :value="getBaseSalary"
              filled
              disabled
            />
            <v-text-field
              label="Sales Cost (EUR)"
              :value="calculateSalesCost"
              filled
              disabled
            />
            <v-text-field
              label="Sales Capacity (PC)"
              :value="calculateSalesCapacity"
              filled
              disabled
            />
          <v-col>
             <v-select
              v-model="selected.selectedSalesActivities.label"
              :items="salesActivities.name"
              @click="setTemp"
              label="Additional sales activities"
              :color="teamColor"
              item-text="name"
              chips
              multiple
              attach
            />
            <v-text-field
              label="New Sales Capacity + Additional Sales Activities (PC)"
              :value="getNewCapacity"
              filled
              disabled
            />
            <v-text-field
              :value="salesActivities.devCosts"
              label="Development Costs for Additional Sales Activities (EUR)"
              type='number'
              filled
              disabled
            />
          </v-col>
          </v-col>
        </v-row>
      </v-tab-item>

      <!-- Manage Sales Prices -->
      <v-tab-item>
        <v-row>
          <v-col>
            <v-text-field
              label="Standard"
              type="number"
              min="0"
              v-model="standardPrice"
              :color="teamColor"
            />
            <v-text-field
              label="Standard Pro"
              type="number"
              min="0"
              v-model="standardProPrice"
              :color="teamColor"
            />
            <v-text-field
              label="Premium"
              type="number"
              min="0"
              v-model="premiumPrice"
              :color="teamColor"
            />
          </v-col>
        </v-row>
      </v-tab-item>
    </v-tabs-items>
    </div>

    <v-row ref="navigation">
        <v-col md='4' align="left">
          <v-btn :color="teamColor" rounded dark @click="toPreviousStep">
            <v-icon left>mdi-arrow-left-bold-circle-outline</v-icon>
            Previous step
          </v-btn>
        </v-col>

        <v-col align="center" md="4">
          <v-btn :color="teamColor" rounded dark @click="toggleDialog" center>
            <v-icon left>mdi-check-outline</v-icon>
            Save changes
          </v-btn>
        </v-col>
      
        <v-col md="4" align="right">
          <v-btn :color="teamColor" rounded right disabled>
            <v-icon left>mdi-arrow-right-bold-circle-outline</v-icon>
            Next step
          </v-btn>
        </v-col>
    </v-row>
    <confirmation-dialog 
      v-if="confirmChangesDialog"
      @closeDialog="toggleDialog"
      @updateProgress="updateProgress"
    ></confirmation-dialog>
    <error-chages-dialog
      v-if="showError"
      @closeError="toggleShowError"
    ></error-chages-dialog>
  </v-container>
</template>

<script>
import prevCurRoundStats from "../components/prevCurRoundStats.vue";
import ConfirmationDialog from "../dialogs/ConfirmationDialog.vue";
import ErrorChagesDialog from '../dialogs/ErrorChagesDialog.vue';
import { mapGetters, mapActions, mapMutations } from 'vuex';

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "sales",
  components: { prevCurRoundStats, ConfirmationDialog, ErrorChagesDialog },
  computed: {
    ...mapGetters('sales', ['vendors', 'vendor']),
    changeValue() {
      return this.numOfSalesPers != 0 ? (this.numOfSalesPers * 0): "";
    },
    getNewCapacity() {
      return this.amount.numOfSalesPersons >= 0 ? (this.amount.numOfSalesPersons * (7000 * this.capacityMultiplier)).toFixed(2): "";
    },
    calculateSalesCost() {
      return this.amount.numOfSalesPersons >= 0 ? (this.amount.numOfSalesPersons * 42366.00).toFixed(2) : "";
    },
    getBaseSalary() {
      return (42366.00).toFixed(2);
    },
    calculateSalesCapacity() {
      return this.amount.numOfSalesPersons >= 0 ? (this.amount.numOfSalesPersons * 7000).toFixed(2) : "";
    },
  },
  data() {
    return {
      showError: false,
      temp: false,
      stepText: '',
      teamColor: this.$store.state.color,
      confirmChangesDialog: false,
      capacityMultiplier: this.calculateCapacityWithMultiplier(),
      salesTabs: null,
      selected: {
        selectedSalesActivities: { 
          label: "", 
          val: 0
        },
      },
      numOfSalesPers: 0,
      salesActivities: {
        name: this.getActivitiesString(),
        devCosts: this.getCalculateSalesActivitiesCost(),
      },
      // salesActivities: this.getActivitiesString(),
      // salesDevelopmentCosts: this.getCalculateSalesActivitiesCost(),
      standardPrice: 0.0,
      standardProPrice: 0.0,
      premiumPrice: 0.0,
      amount: {
        numOfSalesPersons: { label: "Number of Sales Persons", val: 0 },
        pricePerBike: { label: "Price per Bike", val: 0 },
        getSelectedActivity: { label: "Development Cost (EUR)", val: 0}
      },
    };
  },
  methods: {
    ...mapActions('sales', ['vendor']),
    ...mapMutations('sales', ['updateVendor']),
    toggleShowError() {
      this.showError = !this.showError;
    },
    toggleDialog() {
       if(this.selectedSalesActivities === "") {
        this.toggleShowError();
      } else if(this.$store.state.salesStep >= 5){
         this.confirmChangesDialog = !this.confirmChangesDialog;
       }
    },
    updateProgress() {
      // Todo: send data (as oData) to Backend
      this.$emit("updateProgress", "sales", 100);
      //this.$router.push({ path: "/dashboard" });
      this.toggleDialog();
    },
    calculateCosts(selectedSalesActivities) {
      // check for NaN
      if (typeof selectedSalesActivities === "undefined") {
        return "";
      } else {
        return (this.getSelectedActivity * this.numOfSalesPers).toFixed(2);
      }
    },
    toPreviousStep() {
      if(this.$store.state.salesStep >= 5){
        if(this.$store.state.round === 1) {
          this.$router.push({ path: "/bikeConstruction" });
        } else {
          this.$router.push({ path: "/qualityAssurance" }); 
        }
      }
    },
    nextSalesStep() {
       if(this.$store.state.salesStep === 1) {
        this.dataStep();
      } else if(this.$store.state.salesStep === 2) {
        this.logicStep();
      } else if(this.$store.state.salesStep === 3) {
        this.navigationStep();
      } else {
        this.unlockStep();
      }

      this.$store.state.salesStep++;
    },
    dataStep() {
      this.stepText = 'In these tables you can find useful information to previous and current round. Left is previous round and right is current';

      this.$emit('toggleMenuVisability');

      this.setOpacity("round-data", 1);
      this.setOpacity("logic", 0.3);
      this.setOpacity("navigation", 0.3);

      this.setBorder("round-data");
    },
    logicStep() {
      this.stepText = 'Here you can configure your sales processes!'

      this.setOpacity("logic", 1);
      this.setOpacity("round-data", 0.3);

      this.setBorder("logic");
      this.resetBorder("round-data");
    },
    navigationStep() {
      this.stepText = 'You can also navigate through different steps. Do not forget that the steps may differ depending on the round'

      this.setOpacity("navigation", 1);
      this.setOpacity("logic", 0.3);

      this.setBorder("navigation");
      this.resetBorder("logic");
    },
    unlockStep() {
      this.resetBorder("navigation");
      this.setOpacity("round-data", 1);
      this.setOpacity("logic", 1);

      this.$emit('toggleMenuVisability');
    },
    setBorder(name) {
      this.$refs[name].style.border = '5px solid red';
      this.$refs[name].style.borderRadius = '20px';
    },
    resetBorder(name) {
      this.$refs[name].style.border = '0px';
    },
    setOpacity(name, value) {
      this.$refs[name].style.opacity = value;
    },
     createComboBoxItem: function(key, text){
			var salesTypeComboBox = this.getView().byId("salesType");
			var item = new item();
			item.setKey(key);
			item.setText(text);
			salesTypeComboBox.insertItem(item, 10);
			return item;
		},
    getActivitiesString: function(){
			var activities = "";
			if(this.$store.state.round >= 1){
				activities += "Online Shop: 100.000 €"
			}
			else if(this.$store.state.round >= 2){
				activities += "Online Marketing: 200.000 €"
			}
			else if(this.$store.state.round >= 3){
				activities += "Market Analyzer: 800. 000 €"
			}
			else if(this.$store.state.round >= 4){
				activities += "Drone Delivery: 3.000.000 €"
			}
			return activities;
		},
    getCalculateSalesActivitiesCost: function() {
      var cost = 0;
      if(this.$store.state.round >= 1){
        cost += 100000
      }
			else if(this.$store.state.round >= 2){
        cost += 200000;
      }
      else if(this.$store.state.round >= 3){
        cost += 800000;
      }
      else if(this.$store.state.round >= 4){
        cost += 3000000;
      }
      return cost;
    },
		calculateCapacityWithMultiplier: function(){
			var capacity = 1;
			if(this.$store.state.round >= 1){
						capacity*=1.2;
      }
			else if(this.$store.state.round >= 2){
						capacity*=1.2;
			}
      else if(this.$store.state.round >= 3){
						capacity*=1.5;
			}
      else if(this.$store.state.round >= 4){
						capacity*=1.3;
			}
			return capacity;
		},		
  },
  mounted() {
    this.$store.state.innerGuideDone = 
               this.$store.state.purchasingStep >= 5 || 
               this.$store.state.logisticStep >= 5 || 
               this.$store.state.frameStep >= 5 || 
               this.$store.state.sensorStep >= 5 || 
               this.$store.state.bikeStep >= 5 || 
               this.$store.state.salesStep >= 5;

    if(this.$store.state.innerGuideDone) {
      this.$store.state.salesStep = 5;
    }

    if(this.$store.state.salesStep <= 4) {
      this.nextSalesStep();
    }  
  },
  watch: {
    '$store.state.salesStep': function() {
      if(this.$store.state.salesStep === 0) {
        this.$store.state.salesStep++;
        this.nextSalesStep();
      }
    }
  },
};
</script>
